#vstr_feedbacks {
  max-width: 900px;
  box-sizing: border-box;
  margin: 0 auto;
  margin-top: 30px;
  [role="navigation"] {
    display: flex;
    justify-content: center;
    background-color: #fafafa;
    padding: 15px;
    font-size: 16px;
    gap: 7px;
    li {
      margin: 0;
      a {
        border: 1px solid #dfdfdf;
        cursor: pointer;
        padding: 3px 10px;
      }
      &.selected {
        a {
          background-color: #fff;
          border-radius: 3px;
          box-shadow: none;
        }
      }
    }
  }
  .tabs {
    margin: 0;
    background-color: #fff;
    border: 1px solid #ddd;
    list-style: none;
    display: flex;
    li {
      padding: 15px 30px;
      margin: 0;
      cursor: pointer;
      &:hover,
      &.active {
        background-color: blueviolet;
        color: #fff;
      }
    }
  }
  .tab_body {
    padding: 20px;
    background-color: #fff;
    width: 100%;
  }
  .items {
    width: 100%;
    text-align: left;
    background-color: #fff;
    // display: flex;
    // flex-direction: column;
    border-collapse: collapse;

    // tr {
    //   display: flex;
    // }

    td,
    th {
      // &:first-child {
      //   width: 75%;
      // }
      // &:last-child {
      //   width: 25%;
      // }
      padding: 5px 12px;
      border: 1px solid #ddd;
    }
  }
}
